import { AbstractFilter } from './abstract_filter'

export class Match extends AbstractFilter {

    private field: string;
    private data: any = {};

    constructor(field: string, query: any) {
        super();
        this.field = field;
        this.data.query = query;
    }

    public static make(field: string, query: any): Match {
        return new Match(field, query);
    }

    public set(option: string, value: any): this {
        this.data[option] = value;
        return this;
    }

    public boost(value: number = 1): this {
        return this.set('boost', value);
    }

    public fuzzy(value: any = 'AUTO'): this {
        return this.set('fuzziness', value);
    }

    public toJSON() {
        let obj: any = { match: {} };
        obj.match[this.field] = this.data;

        return obj;
    }
}